<template>
  <div class="city">
    <the-navbar />
    <main class="main mt-5 pt-5">
      <div class="container">
        <div class="row">
          <div class="col-12 col-sm-8 col-lg-9">
            <div class="row">
              <div class="col-12">
                <Skyler
                  :cenefa="city.cenefa"
                  :image="city.image"
                  :name="city.name"
                />
              </div>
              <div
                class="col-12 col-sm-6 col-lg-4"
                v-for="(item, index) in pagesByCity"
                :key="index"
              >
                <Site :image="item.image" :name="item.name" :slug="item.slug" />
              </div>
              <div class="col-12 col-lg-3"></div>
            </div>
          </div>
          <div class="col-12 col-sm-4 col-lg-3">
            <div class="row">
              <div class="col-12">
                <Adds />
              </div>
              <div class="col-12">
                <Featureds />
              </div>
              <div class="col-12">
                <Adds />
              </div>
              <div class="col-12">
                <the-footer />
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
    <pre class="container">{{ $data }}</pre>
  </div>
</template>

<script>
import { db } from "@/main";
import axios from "axios";

import Adds from "@/components/Adds";
import Featureds from "@/components/Featureds";
import Site from "@/components/Site";
import Skyler from "@/components/Skyler";
import TheFooter from "@/components/TheFooter";
import TheNavbar from "@/components/TheNavbar";

export default {
  name: "City",
  components: {
    Adds,
    Featureds,
    Site,
    Skyler,
    TheFooter,
    TheNavbar
  },
  data() {
    return {
      pagesByCity: [],
      city: {
        cenefa: "true",
        image: "",
        name: this.$route.params.city
      }
    };
  },
  created() {
    this.getPagesByCity();
  },
  methods: {
    async getPagesByCity() {
      await axios(`${db}/pages`)
        .then(result => {
          this.pagesByCity = result.data;
        })
        .catch(error => {
          var errorCode = error.code;
          var errorMessage = error.message;
          if (errorCode == "auth/weak-password") {
            alert("The password is too weak");
          } else {
            alert(errorMessage);
          }
          console.log(error);
        });
    }
  }
};
</script>

<style scoped></style>
